
.login-overlay{
    position: fixed;
    top: 0;
    left: 0;

    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);

    display: flex;
    justify-content: center;
    align-items: center;

    z-index: 1000;
}

.login-container{
    background-color: white;
    width: 40%;
    height: 90%;
    border-radius: 10px;
    position: relative;
}

.login-title{
    display: inline-block;
}

.close-button{
    display: inline-block;
}

.login-form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: center ;
    margin-top: 20px;
}

.checkbox{
    position: relative;
    top:10px;
    left: 10px;
}

.login-logo{
    position: relative;
    width: 100px;
    top:30px;
}

.terms{
    width: 100%;

    position: absolute;
    bottom: 0;

    display: flex;
    justify-content: center;
}

.login-title{
    font-size: 30px ;
}

.close-button {
    position: absolute;    
    right: 20px;
    top:10px;
    font-size: 20px;
    cursor: pointer;
}

.popup-open {
    animation: popupAnimation 1s ease-in-out;
}

.login-chip{
    width: 80%;
    height: 30px;
    color: white;
    display: flex;

    justify-content: center;
    align-items: center;
    
    background-color: #05203c;
    cursor: pointer;
}

@keyframes popupAnimation{
    0%{transform: scale(1.2);}
    50%{transform: scale(0.8);}
    100%{transform: scale(1);}
}

