.header {
  background-color: #05203c;
  color: white;
  min-height: 400px;
}

.logo{
  width: 50px;
  animation: translateAnim 5s linear infinite;
}

.top-section {
  display: flex;
  flex-wrap: wrap;

  justify-content: space-between;
  align-items: center;
}

.logo-section {
  margin-top: 50px;
  margin-left: 8%;
  
  display: flex;
  flex-direction: row;
}

.text{
  margin-top: 4%;
  margin-left: 10%;
}

.login-section {
  display: flex;
  flex-wrap: wrap;

  gap: 10px; 

  margin-top: 50px;
  margin-right: 100px;
}

.search-section{
  width: 80%;
  min-height: 100px;
/* 
  display: flex;
  flex-wrap: wrap; */
  gap:10px; 

  background-color: #05203c;
  border-radius: 4px;

  justify-content: center;
  align-items: center;
  
  margin-left: auto;
  margin-right: auto;
}

#destination{
  width: 200px;
  height: 80px;
}


#duration{
  width: 200px;
  height: 80px;
}

#persons{
  width: 200px;
  height: 80px;
}

.swap-image{
  position: absolute;
  color: white;
  width: 20px;

  background-color: white;
  border-radius: 50%;
  padding: 5px;
  border: 5px solid #05203c;

  margin-top: 25px;
  margin-left: -20px;

  cursor: pointer;
}
 

.search-button{
  width: 200px;
  height: 80px;
  margin-left: 2%;
  border-radius: 5px;
  border-style: none;
  background-color: #0062e3;
  cursor: pointer;
  color: white;
  font-size: 20px;
}

@media (max-width: 1100px) {
  .search-section{
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 0;
  }
  
  #destination, #duration, #persons{
    width: 80%;
    height: 30px;
    border-radius: 5px;
  }

  .swap-image{
    position: absolute;
    color: white;
    width: 20px;
  
    background-color: white;
    border-radius: 50%;

    border: 5px solid #05203c;
    
    cursor: pointer;
    
    transform: rotate(90deg);
    margin-top: -80px;
  }
  
  
  .search-button{
    width: 80%;
    height: 30px;
    margin-left: 0;
    font-size: 20px;
  }
}

.advertisement{
  margin-top: 50px;

  display: flex;
  flex-direction: row;

  justify-content: space-evenly;
  cursor: pointer;

  font-size: 20px ;
}

.add{
  width: 24%;
  height: 50px;

  background-color: #05203c;
  color: white;
  border-radius: 5px;

  text-align: center;
  align-content: center;

  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

@media (max-width: 600px){
  .advertisement{
    display: flex;
    flex-direction: row;

    box-sizing: border-box;

    width: 100%;
    gap: 10px;

    margin-left: auto;
    margin-right: auto;

    align-items: center;

    overflow-x: scroll;

    scrollbar-width: none;

    align-items: flex-start;
    justify-content: flex-start;   
  }

  .advertisement::-webkit-scrollbar {
    display: none;
  }

  .add{
    height: 100px;
    min-width: 200px;
    /* flex-shrink: 0; */
  }
}

.image-logo{
  position: relative;

  display: flex;
  flex-direction: column;

  width: 80%;
  min-height: 400px;
  height: auto;

  margin-top: 50px;

  margin-left: auto;
  margin-right: auto;

  background-image: url(https://content.skyscnr.com/m/1cf98443c84a84f6/original/Desktop-IN-SING-AU.jpg?imbypass=true);

  background-size: cover;
  background-position: center; 
  background-repeat: no-repeat; 

  color: white;

  overflow-x: scroll;
}

.content{
  display: flex;
  flex-direction: column;
  
  width: 18%;
  flex-wrap: wrap;

  position: absolute;

  left: 10%;
  top: 30%;

}

.footer{
  min-height: 300px;
  margin-top: 10px;

  background-color: #05203c;

  display: grid;

  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-template-areas: 
  'box1 box2 box3'
  'box1 box2 box3'
  'box1 box2 box3'
  'box1 box2 box3'
  'box4 box4 box4';

  justify-content: center;
}

@media (max-width: 1000px) {
  .footer{
    grid-template-areas: 
    'box1'
    'box2'
    'box3'
    'box4'
    ; 
    
    margin-left: auto;
    margin-right: auto;
  }
}

.box{
  margin-top: 40px;
  font-size: 15px;
  font-weight:bold;
  list-style: none;
  text-align: center;
  cursor: pointer;
  color: white;
}

#box1{
  grid-area: box1;

}

#box2{
  grid-area: box2;
}

#box3{
  grid-area: box3;
}

#box4{
  grid-area: box4;
}


@keyframes translateAnim{
  from{transform: rotate(0deg);}
  to{transform: rotate(360deg);}
}
input{
  margin: 5px;
}

.cart, .login{
  cursor: pointer;
  display: flex;
  flex-direction: column;
  align-items: center;
}

li:hover{
  font-size: 18px;
}

.add-title{
  transform: translateX(-1000px);
  transform: translateX(1000px);
}

.scroll-container {                     /* Full width of the container */
  overflow: hidden;                /* Hide the overflow content */
  white-space: nowrap;             /* Prevent text from wrapping */
}

.scroll-text {
  display: inline-block;           /* Keep the text in one line */
  padding-left: 100%;              /* Push the text off the screen to the right */
  animation: scroll 15s linear infinite;
}

@keyframes scroll {
  0% {
    transform: translateX(0);   /* Start off the screen to the right */
  }
  100% {
    transform: translateX(-100%);  /* Move to the left off the screen */
  }
}


.packages{
  display: flex;
  flex-direction: row;
  color: white;
  
  overflow: scroll;
  scrollbar-width: none;

}

.card{
  min-width: 500px;
  height: 200px;

  box-sizing: border-box;
  margin: 20px;
  background-color: #05203c;

  border-radius: 5px;

  display: flex;

  justify-content: center;
  align-items: center;

  cursor: pointer;

  box-shadow: 2px 0 5px 0 #05203c;
}

.card:hover{
  height: 201px;
}

.no-scroll{
  overflow: hidden;
}